<template>
  <li>
    <mcw-card @click="showdetail=!showdetail">
      <mcw-card-primary-action>
        <img class="avater" :src="getavater(entry.name)"/>
      </mcw-card-primary-action>
        <div class="primary name">{{ entry.name }}</div>
        <div class="primary address">{{ entry.address !== 'nan' ? entry.address : "" }}</div>
        <div id="secondery" v-if="showdetail">
          <div class="second" v-if="entry.othername!=='nan'">
            {{ "昵称："+entry.othername }}
          </div>
          <div class="second">
            {{ infoexplain["gender"][entry.gender]+"，"
            +infoexplain["sterilization"][entry.sterilization] }}
          </div>
          <div class="second">
            {{ entry.color+"，"+entry.look }}
          </div>
          <div class="second">
            {{ "性格："+infoexplain["character"][entry.character] }}
          </div>
          <div class="second">{{ "社会关系："+entry.relationship }}</div>
          <div class="second">{{ "现状："+entry.state }}</div>

          <div id='imgs' class="second">
            <img v-for="img in getimglist" :key="img" :src="img">
          </div> 
        </div>
    </mcw-card>
  </li>
</template>
<script>
import infoexplain from '@/assets/info_explain.json';

export default {
  data() {
    return {
      showdetail: false,
      infoexplain,
    };
  },
  props: {
    entry: Object,
  },
  methods: {
    getavater(name) {
      const url = `/photos/${name}.webp`;
      const http = new XMLHttpRequest();

      http.open('HEAD', url, false);
      http.send();

      return http.status !== 404 ? url : '/photos/default.webp';
    },
  },
  computed: {
    getimglist() {
      const ls = [];
      let cnt = 0;
      while (cnt + 1) {
        const url = `/photos/${this.entry.name}${cnt + 1}.webp`;
        const http = new XMLHttpRequest();
        http.open('HEAD', url, false);
        http.send();
        if (http.status !== 200) {
          break;
        } else {
          ls[cnt] = url;
          cnt += 1;
        }
      }
      return ls;
    },
  },
};
</script>
