li{
  margin: 5px 0;
}
li, li *{
  transition: .3s;
}
.mdc-card{
  width: v-bind('showdetail?"100%":"100px"');
}
#secondery{
  display: block;
}
div.second{
  /* width: 100%; */
  text-align: left;
  padding: 2px 50px;
}

.avater{
  width: 100%;
  max-width: 500px;
}

#imgs{
  align-items: center;
  padding: 0;
  margin: 20px 0;
}
#imgs>img{
  display: block;
  width: 100%;
  max-width: 450px;
}

.name{
  font-size: 18px;
  font-weight: bold;
  margin: 5px 0 0 0;
}
.address{
  width: 100%;
  font-size: 14px;
  margin: 0 0 5px 0;
}
</style>
